import React, { useState } from 'react';
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    TextField,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
} from '@mui/material';
import { useDispatch } from 'react-redux';
import { addTransaction, updateTransaction } from '../store/transactionSlice';
import { Transaction } from '../types/transaction';
import { v4 as uuidv4 } from 'uuid';

interface TransactionFormProps {
    open: boolean;
    onClose: () => void;
    editingTransaction?: Transaction;
}

const TransactionForm: React.FC<TransactionFormProps> = ({ open, onClose, editingTransaction }) => {
    const dispatch = useDispatch();
    const [transaction, setTransaction] = useState<Omit<Transaction, 'id'>>({
        date: editingTransaction?.date || new Date().toISOString().split('T')[0],
        type: editingTransaction?.type || 'expense',
        amount: editingTransaction?.amount || 0,
        category: editingTransaction?.category || '',
        description: editingTransaction?.description || '',
    });

    React.useEffect(() => {
        if (editingTransaction) {
            setTransaction({
                date: editingTransaction.date,
                type: editingTransaction.type,
                amount: editingTransaction.amount,
                category: editingTransaction.category,
                description: editingTransaction.description,
            });
        }
    }, [editingTransaction]);

    const handleSubmit = () => {
        if (editingTransaction) {
            dispatch(updateTransaction({
                ...transaction,
                id: editingTransaction.id
            }));
        } else {
            const newTransaction: Transaction = {
                ...transaction,
                id: uuidv4(),
            };
            dispatch(addTransaction(newTransaction));
        }
        onClose();
    };

    return (
        <Dialog open={open} onClose={onClose}>
            <DialogTitle>{editingTransaction ? '编辑交易' : '新增交易'}</DialogTitle>
            <DialogContent>
                <TextField
                    margin="dense"
                    label="日期"
                    type="date"
                    fullWidth
                    value={transaction.date}
                    onChange={(e) =>
                        setTransaction({ ...transaction, date: e.target.value })
                    }
                    InputLabelProps={{ shrink: true }}
                />
                <FormControl fullWidth margin="dense">
                    <InputLabel id="transaction-type-label">类型</InputLabel>
                    <Select
                        labelId="transaction-type-label"
                        label="类型"
                        value={transaction.type}
                        onChange={(e) =>
                            setTransaction({
                                ...transaction,
                                type: e.target.value as 'income' | 'expense',
                            })
                        }
                    >
                        <MenuItem value="income">收入</MenuItem>
                        <MenuItem value="expense">支出</MenuItem>
                    </Select>
                </FormControl>
                <TextField
                    margin="dense"
                    label="金额"
                    type="number"
                    fullWidth
                    value={transaction.amount}
                    onChange={(e) =>
                        setTransaction({
                            ...transaction,
                            amount: parseFloat(e.target.value),
                        })
                    }
                />
                <TextField
                    margin="dense"
                    label="类别"
                    fullWidth
                    value={transaction.category}
                    onChange={(e) =>
                        setTransaction({ ...transaction, category: e.target.value })
                    }
                />
                <TextField
                    margin="dense"
                    label="描述"
                    fullWidth
                    multiline
                    rows={4}
                    value={transaction.description}
                    onChange={(e) =>
                        setTransaction({
                            ...transaction,
                            description: e.target.value,
                        })
                    }
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose}>取消</Button>
                <Button onClick={handleSubmit} variant="contained" color="primary">
                    保存
                </Button>
            </DialogActions>
        </Dialog>
    );
};

export default TransactionForm;